import React from 'react'

const nav = () => {
    return (
        <div>
            <nav className="navbar px-3 navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <a className="navbar-brand" href="#">PelisPedia</a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarColor02" aria-controls="navbarColor02" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarColor02">
            <ul className="navbar-nav me-auto">
              <li className="nav-item">
                <a className="nav-link active" href="#">Peliculas
                  <span className="visually-hidden">(current)</span>
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="#">Series</a>
              </li>
              
              <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle" data-bs-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">Ver más</a>
                <div className="dropdown-menu">
                  <a className="dropdown-item" href="#">Favoritos</a>
                  <a className="dropdown-item" href="#">Ultimos añadidos</a>
                  <a className="dropdown-item" href="#">Ver por genero</a>
                  <div className="dropdown-divider" />
                  <a className="dropdown-item" href="#">Proximamente</a>
                </div>
              </li>
            </ul>
            <form className="d-flex">
              <input className="form-control me-sm-2" type="text" placeholder="Buscar" />
              <button className="btn btn-secondary my-2 my-sm-0" type="submit">Buscar</button>
            </form>
          </div>
        </div>
      </nav>
        </div>
    )
}

export default nav
